<template>
  <div>
    <v-container>
      <v-row
        v-for="(v, k) in AbilityDescriptions"
        :key=k
        >

        <v-col
          v-if="v.abtype == 'buff'"
          cols="2">
          {{ v.text }}
        </v-col>

        <v-col
          v-if="v.abtype == 'buff'"
          cols="1">
          {{ value[k] }}
        </v-col>

        <v-col
          v-if="v.abtype == 'buff'"
          cols="9">
          <PMSlider
            :value="value[k]"
            :size=v.maxSize
            :disableLabels="v.maxSize > 15"
            v-on:input="emitAbilityValues(k, $event)"
            />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
  import PMSlider from '../molecules/PMSlider'

  export default {
    components: {
      PMSlider,
    },
    props: {
      value: {
        type: Object,
        default: function() {
          return {
            "allrounder1": 0,
            "allrounder2": 0,
            "slowstarter": 0,
            "startdash": 0,
            "popular": 0,
            "quiet": 0,
            "memHigh": 0,
            "memLow": 0,
            "melancholy1": 0,
            "melancholy2": 0,
            "attention": 0,
            "modest": 0,
            "perfectly": 0,
            "mehealPlus": 0,
            "mehealMinus": 0,
            "bond": 0,
          }
        },
      },
    },
    data () {
      return {
        AbilityDescriptions: {
          "slowstarter":  { "abtype": "buff",   "maxSize": 5,  "text": "",},
          "startdash":    { "abtype": "buff",   "maxSize": 5,  "text": "",},
          "popular":      { "abtype": "buff",   "maxSize": 5,  "text": "",},
          "quiet":        { "abtype": "buff",   "maxSize": 5,  "text": "",},
          "memHigh":      { "abtype": "buff",   "maxSize": 5,  "text": "",},
          "memLow":       { "abtype": "buff",   "maxSize": 5,  "text": "",},
          "perfectly":    { "abtype": "buff",   "maxSize": 5,  "text": "",},
          "bond":         { "abtype": "buff",   "maxSize": 20, "text": "",},
          "melancholy1":  { "abtype": "effect", "maxSize": 5,  "text": "+",},
          "melancholy2":  { "abtype": "effect", "maxSize": 5,  "text": "++",},
          "attention":    { "abtype": "effect", "maxSize": 5,  "text": "",},
          "modest":       { "abtype": "effect", "maxSize": 5,  "text": "",},
          "mehealPlus":   { "abtype": "effect", "maxSize": 5,  "text": "+",},
          "mehealMinus":  { "abtype": "effect", "maxSize": 5,  "text": "-",},
        },
      }
    },
    methods: {
      emitAbilityValues(k, v) {
        this.value[k] = v
        this.$emit('input', this.value)
      }
    },
  }
</script>
